$main-color: #e30b20;
$logo-size: 160px;
$joycon-gutter: $logo-size * 0.05;
$joycon-border-width: $logo-size * 0.075;
$joycon-border-radius: $logo-size * 0.225;
$joycon-width: $logo-size * 0.5 - $joycon-gutter;
$stick-size: $logo-size * 0.2;
$stick-position-left: $logo-size * 0.05;
$stick-left-position-top: $logo-size * 0.12;
$stick-right-position-top: $logo-size * 0.375;

.switch-background {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: $main-color; }

.switch-logo {
  display: inline-flex;
  justify-content: space-between;
  align-items: stretch;
  width: $logo-size;
  height: $logo-size;
  margin: auto;

  &:active {
    &,
    & *,
    & *:before,
    & *:after {
      animation: reset; } } }

.joycon-left, .joycon-right {
  position: relative;
  width: $joycon-width;
  border: $joycon-border-width solid white;

  &:after {
    content: '';
    position: absolute;
    width: $stick-size;
    height: $stick-size;
    left: $stick-position-left;
    border-radius: 50%; } }

.joycon-left {
  border-radius: $joycon-border-radius 0 0 $joycon-border-radius;
  animation: joycon-left-animation .6s linear 1 1s forwards;

  &:after {
    top: $stick-left-position-top;
    background-color: white; } }

.joycon-right {
  border-radius: 0 $joycon-border-radius $joycon-border-radius 0;
  background-color: white;
  transform: translateY(-50%);
  animation: joycon-right-animation .6s linear 1 1s forwards;

  &:after {
    top: $stick-right-position-top;
    background-color: $main-color; } }

@keyframes joycon-left-animation {
  0% {
    transform: translateY(0); }
  60% {
    transform: translateY(0); }
  75% {
    transform: translateY(10%); }
  100% {
    transform: translateY(0%); } }

@keyframes joycon-right-animation {
  0% {
    transform: translateY(-50%); }
  40% {
    transform: translateY(-55%); }
  60% {
    transform: translateY(0); }
  75% {
    transform: translateY(10%); }
  100% {
    transform: translateY(0%); } }

