$color-blue-light: #9adcf9;
$color-blue: #4dc6f6;
$color-blue-deep: #3e75d8;
$color-blue-dark: #3363c9;
$color-gray: #e6e6e6;
$icon-size: 128px;
$inbox-header-height: $icon-size * 0.375;
$inbox-footer-height: $icon-size * 0.625;

.inbox-background {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; }

.inbox-wrap {
  position: relative;
  margin: 0 auto;
  width: $icon-size;
  height: $icon-size;

  &:after {
    content: '';
    position: absolute;
    top: $inbox-header-height;
    right: 0;
    width: 146px;
    height: 146px;
    transform: rotateZ(-32deg) skewX(10deg);
    transform-origin: top right;
    background: linear-gradient(to bottom, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 0) 50%);
    filter: blur(5px); }

  &:active {
    &,
    & *,
    & *:before,
    & *:after {
      animation: reset; } } }

.inbox {
  overflow: hidden;
  position: relative;
  z-index: 1;
  border-radius: 10px; }

.inbox-header {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: $inbox-header-height;
  transform: rotateX(90deg);
  transform-origin: bottom center;
  animation: header-paper-open .25s linear 1 1.25s forwards;

  .header-paper {
    transform: rotateX(45deg);

    &:after {
      content: '';
      position: absolute;
      left: calc(50% - 49px);
      top: 19px;
      width: 98px;
      height: 98px;
      background-color: $color-blue-dark;
      transform: rotateZ(45deg);
      border-radius: 6px 12px 12px; } } }

.inbox-footer {
  position: relative;
  width: $icon-size;
  height: $inbox-footer-height;
  background-color: $color-blue-deep; }

.paper-mask {
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: 20;
  width: 100%;
  transform: rotateX(0deg);
  transform-origin: top center;
  animation: paper-mask-open .25s linear 1 1s forwards;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 80px;
    height: 80px;
    transform: rotateZ(-35deg) skewX(20deg);
    transform-origin: top right;
    background: rgba(0, 0, 0, .025);
    filter: blur(2px); }

  &:after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    background-color: white;
    clip-path: polygon(0 0, 100% 0, 100% 5%, 50% 60%, 0 5%); } }

.inbox-footer-paper-left, .inbox-footer-paper-right {
  position: absolute;
  z-index: 10;
  width: $icon-size;
  height: $inbox-footer-height;
  background-color: white;
  border-radius: 0 0 10px 10px;

  &:after {
    content: '';
    position: absolute;
    top: calc(50% - (#{$icon-size}));
    left: calc(50% - (#{$icon-size}));
    width: $icon-size * 2;
    height: $icon-size * 2;
    border-radius: 50%;
    transform: scale(0);
    opacity: 0;
    animation: inbox-footer-paper-fill .5s 1 1.25s forwards; } }

.inbox-footer-paper-left {
  clip-path: polygon(0 0, 0% 100%, 100% 100%);

  &:after {
    background-color: $color-blue; } }

.inbox-footer-paper-right {
  clip-path: polygon(100% 0, 0% 100%, 100% 100%);

  &:after {
    background-color: $color-blue-light; } }

.inbox-check {
  position: absolute;
  left: 50%;
  top: -20%;
  transform: translateY(150%);
  animation: inbox-check-rise .5s 1 1.25s forwards;

  &:before {
    content: '';
    display: block;
    width: 68px;
    height: 36px;
    border-left: 12px solid $color-gray;
    border-bottom: 12px solid $color-gray;
    transform: rotate(-45deg);
    transform-origin: bottom left; }

  &:after {
    content: '';
    position: absolute;
    top: 45%;
    left: 70%;
    width: 68px;
    height: 68px;
    transform: rotate(-45deg);
    transform-origin: bottom left;
    background: linear-gradient(to bottom right, rgba(0, 0, 0, .2) 0%, rgba(0, 0, 0, 0) 40%); } }

@keyframes inbox-footer-paper-fill {
  from {
    opacity: 0;
    transform: scale(0); }
  to {
    opacity: 1;
    transform: scale(1); } }

@keyframes inbox-check-rise {
  from {
    transform: translateY(150%); }
  to {
    transform: translateY(0%); } }

@keyframes paper-mask-open {
  from {
    transform: rotateX(0deg); }
  to {
    transform: rotateX(90deg); } }

@keyframes header-paper-open {
  from {
    transform: rotateX(90deg); }
  to {
    transform: rotateX(0deg); } }
